
import XHeader from 'vux/src/components/x-header/index.vue'
import Box from 'vux/src/components/box/index.vue'

import circleTitle from './../../../components/circleTitle/circleTitle'

import rate_02 from '@/assets/images/pay/rate_02.png'
import rate_03 from '@/assets/images/pay/rate_06.png'
import rate_04 from '@/assets/images/pay/rate_03.png'
import rate_05 from '@/assets/images/pay/rate_04.png'

export default {
name: "rate",
data() {
return {
userList:[
{
img:rate_05,
text:'金级',
title:'您当前是金级代理',
level:4
},
{
img:rate_04,
text:'合伙人',
title:'您当前是合伙人',
level:3
},
{
img:rate_03,
text:'高级会员',
title:'您当前是高级会员',
level:2
},
{
img:rate_02,
text:'会员',
title:'您当前是会员',
level:1
},
],
level:'',
levelText:'',
rateList:[]
}

},
components: {
XHeader,Box,circleTitle
},
created() {
this.$axios.post('/api/api/userFeilv')
.then(res=>{
console.log(res)
if(res.code==1){
this.level = res.result.level;

}else{
mui.toast(res.msg)
}
})
},
mounted() {
},
methods: {

},
watch: {
'level'(){
switch (this.level) {
case 1:
this.levelText = '您当前是会员';
break;
case 2:
this.levelText = '您当前是高级会员';
break;
case 3:
this.levelText = '您当前是合伙人';
break;
default:
this.levelText = '您当前是金级代理';
break;
}

}
}
}



